* {
  scroll-behavior: smooth;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  height: 100vh;
  background: -webkit-gradient(linear, left top, right top, from(rgb(255, 245, 60)), to(rgba(252,186,14,1)));
  background: -webkit-linear-gradient(left, rgb(255, 245, 60) 0%, rgba(252,186,14,1) 100%);
  background: -o-linear-gradient(left, rgb(255, 245, 60) 0%, rgba(252,186,14,1) 100%);
  background: linear-gradient(90deg, rgb(255, 245, 60) 0%, rgba(252,186,14,1) 100%);
}



